body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.row{width:100%;display:block}.row [class*="col-"]{display:inline-block;width:100%;vertical-align:top}@media only screen and (min-width: 650px){.row [class*="col-"].col-1{width:8.33%}.row [class*="col-"].col-2{width:16.66%}.row [class*="col-"].col-3{width:25%}.row [class*="col-"].col-4{width:33.33%}.row [class*="col-"].col-5{width:41.66%}.row [class*="col-"].col-6{width:50%}.row [class*="col-"].col-7{width:58.33%}.row [class*="col-"].col-8{width:66.33%}.row [class*="col-"].col-9{width:75%}.row [class*="col-"].col-10{width:83.33%}.row [class*="col-"].col-11{width:91.66%}.row [class*="col-"].col-12{width:100%}}.row [class*="col-"]:not(:last-child)>*{width:calc(100% - 32px)}div.card{width:calc(100% - 24px);padding:4px 8px}div.card+div.card{margin-top:16px}div.card .card-title{text-align:center}div.card h1{font-size:1.3em}.dark div.card h1{color:#000}.light div.card h1{color:#000}div.card h2{font-size:1.2em}.dark div.card h2{color:#202870}.light div.card h2{color:#44d}div.card h3{font-size:1.15em}.dark div.card h3{color:#501878}.light div.card h3{color:#c2a}div.card h4{font-size:1.05em}div.card h5{font-size:0.95em}div.card h6{font-size:0.9em}

.main-page{width:100%;min-height:100vh;position:relative}.dark .main-page{background:url(/static/media/stars.3a97ce1c.png),linear-gradient(#1e0b42 30%, #101828 80%)}.light .main-page{background:url(/static/media/stars.3a97ce1c.png),linear-gradient(#9183ac 30%, #2b5580 80%)}.main-page .container{padding:24px 8px;margin:0 8%;min-height:calc(100vh - 128px - 36px - 112px)}.dark .main-page .container{background:#ccc;color:#101820}.light .main-page .container{background:#fff;color:#101820}

.remove-link-decoration{color:inherit;text-decoration:none;display:block}div.banner{width:100%;min-height:96px;text-align:center;line-height:64px;padding-top:32px;font-size:2.4em}.dark div.banner{font-family:"Codystar",cursive;background:url(/static/media/stars.3a97ce1c.png),linear-gradient(#101820 40%, #1e0b42);color:#fff}.light div.banner{font-family:"Knewave",cursive;background:url(/static/media/stars.3a97ce1c.png),linear-gradient(#4588cc 40%, #9183ac);color:#fff}

nav.main-nav{font-size:1.1em;font-family:verdana;margin:0 8%}nav.main-nav ul.nav-list{list-style:none;margin:0;padding:0}nav.main-nav ul.nav-list li{width:25%;height:36px;line-height:36px;display:inline-block;text-align:center}nav.main-nav ul.nav-list li a{text-decoration:none;width:100%;display:inline-block;height:100%}.dark nav.main-nav ul.nav-list li a{background:url(/static/media/stars.3a97ce1c.png),#1e0b42;background-position:50px 30px;color:#fff}.light nav.main-nav ul.nav-list li a{background:url(/static/media/stars.3a97ce1c.png),#9183ac;background-position:50px 30px;color:#fff}.dark nav.main-nav ul.nav-list li a:hover{background:url(/static/media/stars.3a97ce1c.png),rgba(26,69,99,0.741176);background-position:50px 30px}.light nav.main-nav ul.nav-list li a:hover{background:url(/static/media/stars.3a97ce1c.png),rgba(106,180,230,0.431373);background-position:50px 30px}

footer.main-footer{text-align:center;height:64px;line-height:64px;color:white}

